// Expander Tweaks

.nhsuk-details__summary:before {
    bottom: 0;
    content: '';
    left: 0;
    margin: auto;
    position: absolute;
    top: 0;
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-color: transparent;
    clip-path: polygon(0% 0%, 100% 50%, 0% 100%);
    border-width: 7px 0 7px 12.124px;
    border-left-color: inherit;
}

.nhsuk-details__above--third {
    width: 33%;
    display: inline-block;
    .case-type-center {
        text-align: center;
    }
    p {
        margin-bottom: 0px;
        &.case-stage-title {
            margin-bottom: 32px;
        }
        &.status-title {
            margin-bottom: 32px;
            text-align: center;
        }
        &.edit-title {
            margin-bottom: 32px;
            text-align: right;
        }
    }
}

.nhsuk-details__summary-text--third {
    width: calc(33.33% - 20px);
    display: inline-block;
    .case-type-center {
        text-align: center;
    }
    p {
        margin-bottom: 0px;
    }
    a {
        line-height: 15px;
        float: right;
        margin-bottom: 0px;
    }
}

.nhsuk-details__summary-text-center {
    text-align: center;
}

.nhsuk-details {
    background-color: #ffffff;
    border-top: 4px solid transparent;
    display: inline-block;
    padding: 20px 24px 24px;
    width: 100%;
}

.nhsuk-expander-group {
    width: 100%;
    display: block;
}

.table-text-small {
    font-size: 14px;
    color: grey;
}

.nhsuk-table__button--incomplete {
    color: #ffb81C !important;
    border: 2px solid #ffb81C;
}

.nhsuk-table__button--incomplete:hover {
    color: white!important;
    background-color: #ffb81C;
}

.nhsuk-table__button--not-started {
    color: 	#768692!important;
    border: 2px solid #768692;
}

.nhsuk-table__button--not-started:hover {
    color: white!important;
    background-color: 	#768692;
}

.nhsuk-expander .nhsuk-details__summary {
    padding: 0px!important;
}

.nhsuk-expander .nhsuk-details__summary-text {
    background: none!important;


}
.nhsuk-expander .nhsuk-details__summary:focus .nhsuk-details__summary-text {
    background-color: #ffeb3b!important;
    box-shadow: 0 -2px #ffeb3b, 0 4px #212b32;
    color: #212b32;
    outline: 4px solid transparent;
    text-decoration: none;
    background: #ffeb3b ;

}

.nhsuk-expander .nhsuk-details__summary-text:before {
    bottom: 0;
    content: '';
    left: 0;
    margin: auto;
    position: absolute;
    top: 0;
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-color: transparent;
    clip-path: polygon(0% 0%, 100% 50%, 0% 100%);
    border-width: 7px 0 7px 12.124px;
    border-left-color: inherit;
}
.nhsuk-expander[open] .nhsuk-details__summary:focus .nhsuk-details__summary-text {

    background-color: #ffeb3b!important;
    box-shadow: 0 -2px #ffeb3b, 0 4px #212b32;
    color: #212b32;
    outline: 4px solid transparent;
    text-decoration: none;
}

.nhsuk-expander[open] .nhsuk-details__summary-text:before {
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-color: transparent;
    clip-path: polygon(0% 0%, 50% 100%, 100% 0%);
    border-width: 12.124px 7px 0 7px;
    border-top-color: inherit;
}

// NEW SIDEBAR


.nhsBSA-sidebar-container {
    display: flex;
    flex-direction: column;
    background-color: $color_nhsuk-grey-5;
    min-height: calc(100vh - 99px);
    margin: 0;
    padding: 0;
    @media (min-width: 48.0525em) {
      min-height: calc(100vh - 80px);
    }

    .nhsBSA-sidebar-wrapper {
      flex: 1 0 300px;
      display: flex;

      .nhsBSA-sidebar-content {
        flex: 1 0 300px;
        background-color: $color_nhsuk-grey-5;
        padding: 0px;
        margin: 0px;


        .nhsuk-main-wrapper {
          padding-top: 40px;
          padding-bottom: 40px;
          display: block;

          @media (min-width: 40.0625em) {
              padding-top: 32px;
          }
          @media (min-width: 40.0625em) {
              padding-bottom: 32px;
          }
        }

      }

      .nhsBSA-sidebar-nav {
        width: 300px;
        order: -1;
        background-color: $color_nhsuk-grey-4;
        margin: 0;

        .nhsuk-back-link {
          padding: 16px;
          margin: 0;
          @media (min-width: 48.0525em) {
            padding: 32px;
            margin: 0;
          }
        }
      }


    }


    @media (max-width: 48.0525em) {
      .nhsBSA-sidebar-wrapper {
         flex-direction: column;

        .nhsBSA-sidebar-nav {
          width: 100%;
        }
      }
    }

  }

