.timeline {
    padding: calc(2 * #{5px});
    margin: 0 auto;

    .action_needed {
      background-color: #DEE0E2;
      padding: 5px 20px 5px 15px;

      h2 {
        font-size: 1.2em;
      }

      &:before {
        content: "";
        display: block;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        background-color: #6e777a;
        border: 8px solid #6f777b;
        position: absolute;
        z-index: 2;
        left: -35px;
        top: 16px;
      }
    }

    ul {
      list-style-type: none;
      position: relative;
      padding: 0 0 0 10px;
      margin: 0 0 20px 0;
      &:after {
        content: "";
        position: absolute;
        top: 45px;
        left: -4.5px;
        bottom: 0;
        border-left: 4px solid #6f777b;
        height: 96%;
      }

      li {
        margin: 4px 0 20px 10px;
        padding-left: 15px;
        position: relative;
        display: block;
        border-bottom: 1px solid #DEE0E2;
        &:last-child {
          margin-bottom: 0px;
          border-bottom: none;
        }

        .timeline-reference {
          @extend .nhsuk-heading-s;
          display: table;
          position: relative;
          &:before {
            content: "";
            position: absolute;
            left: -20px;
            width: 20px;
            top: 9px;
            z-index: -1;
          }
        }

        &:before {
          content: "";
          display: block;
          width: 15px;
          height: 15px;
          border-radius: 50%;
          background-color: white;
          border: 4px solid #6F777B;
          position: absolute;
          z-index: 2;
          left: -31px;
          top: 0px;
        }

        h2 {
          @extend .nhsuk-heading-m;
          margin-top: 0.5em;
          font-size: 0.9em;
        }
      }
    }
