/* ==========================================================================
   COMPONENTS/ #ACTION-LINK
   ========================================================================== */


/**
 * 1. Display is inline-block so the top and bottom margins/paddings are
 *    respected.
 * 2. 'Random number' is used to properly have sufficient space between icon
 *    and text.
 * 3. Position is relative so the arrow icon can display absolute.
 * 4. Text decoration none used to override default <a> styling.
 * 5. Box shadow 8px used instead of the default 4px.
 * 6. Text decoration underline used to override default <a> styling.
 */

.nhsuk-action-link {
    margin-bottom: 32px;
}

@media (min-width: 40.0625em) {
    .nhsuk-action-link {
        margin-bottom: 40px;
    }
}

.nhsuk-action-link__link {
    font-weight: 400;
    font-size: 18px;
    font-size: 1.125rem;
    line-height: 1.55556;
    display: inline-block;
    /* [1] */
    font-weight: 600;
    padding-left: 38px;
    /* [2] */
    position: relative;
    /* [3] */
    text-decoration: none;
    /* [4] */
}

@media (min-width: 40.0625em) {
    .nhsuk-action-link__link {
        font-size: 22px;
        font-size: 1.375rem;
        line-height: 1.45455;
    }
}

@media print {
    .nhsuk-action-link__link {
        font-size: 18pt;
        line-height: 1.15;
    }
}

.nhsuk-action-link__link:hover .nhsuk-action-link__text {
    text-decoration: underline;
    /* [6] */
}

.nhsuk-action-link__link:focus {
    background-color: #ffeb3b;
    box-shadow: 0 -2px #ffeb3b, 0 4px #212b32;
    color: #212b32;
    outline: 4px solid transparent;
    text-decoration: none;
}

.nhsuk-action-link__link:focus:hover .nhsuk-action-link__text {
    color: #212b32;
    text-decoration: none;
}

@media (max-width: 40.0525em) {
    .nhsuk-action-link__link {
        padding-left: 26px;
        /* [2] */
    }
}

@media print {
    .nhsuk-action-link__link {
        color: #212b32;
    }
    .nhsuk-action-link__link:visited {
        color: #212b32;
    }
}

.nhsuk-action-link__link .nhsuk-icon__arrow-right-circle {
    fill: #007f3b;
    height: 36px;
    left: -3px;
    position: absolute;
    top: -2px;
    width: 36px;
}

@media print {
    .nhsuk-action-link__link .nhsuk-icon__arrow-right-circle {
        color: #212b32;
        fill: #212b32;
    }
    .nhsuk-action-link__link .nhsuk-icon__arrow-right-circle:active,
    .nhsuk-action-link__link .nhsuk-icon__arrow-right-circle:focus,
    .nhsuk-action-link__link .nhsuk-icon__arrow-right-circle:visited {
        color: #212b32;
    }
}

@media (max-width: 40.0525em) {
    .nhsuk-action-link__link .nhsuk-icon__arrow-right-circle {
        height: 24px;
        left: -2px;
        margin-bottom: 0;
        top: 2px;
        width: 24px;
    }
}


