/**
 * 1. Allow space for the arrow.
 * 2. Align the icon with the start of the back link.
 * 3. Align the icon with the middle of the text.
 */

.nhsuk-back-link {
    margin-bottom: 16px;
}

.nhsuk-back-link__link {
    font-size: 14px;
    font-size: 0.875rem;
