.nhsuk-details__summary {
    color: #005eb8;
    /* [1] */
    cursor: pointer;
    display: inline-block;
    /* [2] */
    padding-left: 24px;
    position: relative;
    /* [3] */
}

.nhsuk-details__summary:hover {
    color: #7C2855;
}

.nhsuk-details__summary:before {
    bottom: 0;
    content: '';
    left: 0;
    margin: auto;
    position: absolute;
    top: 0;
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-color: transparent;
    clip-path: polygon(0% 0%, 100% 50%, 0% 100%);
    border-width: 7px 0 7px 12.124px;
    border-left-color: inherit;
}

.nhsuk-details__summary:focus {
    background-color: #ffeb3b;
    box-shadow: 0 -2px #ffeb3b, 0 4px #212b32;
    color: #212b32;
    outline: 4px solid transparent;
    text-decoration: none;
}

.nhsuk-details__summary:focus .nhsuk-icon {
    fill: #212b32;
}

.nhsuk-details__summary:hover .nhsuk-details__summary-text,
.nhsuk-details__summary:focus .nhsuk-details__summary-text {
    text-decoration: none;
}

.nhsuk-details[open]>.nhsuk-details__summary:before {
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-color: transparent;
    clip-path: polygon(0% 0%, 50% 100%, 100% 0%);
    border-width: 12.124px 7px 0 7px;
    border-top-color: inherit;
}

.nhsuk-details__summary-text {
    text-decoration: underline;
    /* [4] */
}

.nhsuk-details__summary::-webkit-details-marker {
    display: none;
    /* [5] */
}

.nhsuk-details__text {
    border-left: 4px solid #d8dde0;
    margin-top: 8px;
    padding: 16px;
    padding-left: 20px;
    /* [6] */
}

.nhsuk-details__text>*:first-child {
    margin-top: 0;
}

.nhsuk-details__text>*:last-child {
    margin-bottom: 0;
}

/**
 * Expander variation.
 *
 * 1. !important used because the icon is populated
 *    by the JavaScript polyfill
 * 2. Remove the default hover, focus and active
 *    styles for this component.
 * 3. -2px left margin to align the icon to the content.
 * 4. When a group of details is used reduce the
 *    margin between them so they sit together.
